.section-intro {
  display: flex;
  justify-content: center;
  align-items: end;
  background-color: $color-pink-1;
  height: 664px;
  width: auto;
}

.intro__components {
  @extend %flex-space-between;
  align-items: end;
  height: 100%;
  position: relative;
  z-index: 1;
}

.intro {
  &__left {
    display: flex;
    flex-direction: column;
  }

  &__title {
    display: flex;
    flex-direction: column;
    margin: 0;

    &-1 {
      @extend %titan-400;
      letter-spacing: 1.2px;
      font-size: 38px;
      color: $color-white;
    }

    &-2 {
      @extend %titan-400;
      letter-spacing: 2.32px;
      font-size: 58px;
      color: $color-red-1;
      line-height: 0.95;
    }
  }
}

//milk
.intro__milk {
  display: flex;
  padding: 0 0 60px;

  &__img {
    width: 213px;
    height: 216px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: end;
    text-align: left;
    width: 200px;
    @extend %dmSans-400;
    color: $color-white;
    font-size: 14px;
    line-height: 1.4;
    padding-bottom: 24px;
  }

  &__info-benefit {
    margin: 0 0 20px;
  }
}

//Right  section

.intro__right {
  display: flex;
}

.intro__numbers {
  display: flex;
  flex-direction: column;
  justify-content: end;
  &-list {
    list-style: none;
    text-align: right;
    margin: 0;
    padding: 0 18px 90px 0;
    display: flex;
    flex-direction: column;
    gap: 18px;
  }

  &-number {
    @extend %titan-400;
    color: $color-red-1;
    font-size: 46px;
    letter-spacing: 1.84px;
    margin: 0 0 2px;
  }

  &-property {
    @extend %dmSans-400;
    color: $color-white;
    font-size: 14px;
    margin: 0;
  }
}

.intro__images {
  &-ice-cream {
    width: 236px;
    height: 557px;
    margin-right: 8px;
    display: flex;
    justify-content: end;
  }

  &-girl {
    display: flex;
    justify-content: end;
    width: 304px;
    height: 241px;
  }

  &-circle-back {
    position: absolute;
    z-index: -1;
    top: 54px;
    right: 149px;
  }

  &-circles-white {
    position: absolute;
    z-index: -1;
    list-style: none;
    top: 152px;
    right: 16px;
    display: flex;
    flex-direction: column;
    gap: 62px;
  }

  &-circle-white {
    width: 30px;
    height: 30px;
    &-icon {
      width: 100%;
      height: 100%;
    }
  }
}
